import Request from './request'

const http = new Request()

http.setConfig((config) => { /*  */
  config.baseUrl = 'https://feiyan.libingtao.cn' /*  */
  config.header = {
    ...config.header
  }
  return config
})

/**
 * true (resolve)(reject)
 * @param { Number } statusCode - statusCode
 * @return { Boolean } true, resolve,  reject
 */
http.validateStatus = (statusCode) => {
  return statusCode === 200
}

http.interceptor.request((config, cancel) => { /*  */
  config.header = {
    ...config.header
  }
  /*
  if (!token) { // tokencancel catch() 
    cancel('token ') // catch((err) => {}) err.errMsg === 'token '
  }
  */
  return config
})

http.interceptor.response((response) => { /*  */
  // if (response.data.code !== 200) { // 200reject()
  //   return Promise.reject(response)
  // }
  // if (response.config.custom.verification) { // 
  //   return response.data
  // }
  return response
}, (response) => { // 
  return response
})

export {
  http
}
