/* 
	mixinuni-appuni.request()
 */
export default {
	data() {
		return {

		}
	},
	computed: {

	},
	methods: {
		zeroize(num) {
			return (String(num).length == 1 ? '0' : '') + num;
		},
		add0(m){
			return m<10?'0'+m:m;
		},
		timeFormat(timestamp){
		  //timestampparseInt,0
		    var time = new Date(timestamp);
		    var year = time.getFullYear();
		    var month = time.getMonth()+1;
		    var date = time.getDate();
		    var hours = time.getHours();
		    var minutes = time.getMinutes();
		    var seconds = time.getSeconds();
		    return year+'-'+this.add0(month)+'-'+this.add0(date)+' '+this.add0(hours)+':'+this.add0(minutes)+':'+this.add0(seconds);
		},
		//,
		timestampFormat(timestamp) {

			var curTimestamp = parseInt(new Date().getTime() / 1000); //
			var timestampDiff = curTimestamp - timestamp; // 

			var curDate = new Date(curTimestamp * 1000); // 
			var tmDate = new Date(timestamp * 1000); // 

			var Y = tmDate.getFullYear(),
				m = tmDate.getMonth() + 1,
				d = tmDate.getDate();
			var H = tmDate.getHours(),
				i = tmDate.getMinutes(),
				s = tmDate.getSeconds();

			if (timestampDiff < 60) { // 
				return "";
			} else if (timestampDiff < 3600) { // 
				return Math.floor(timestampDiff / 60) + "";
			} else if (curDate.getFullYear() == Y && curDate.getMonth() + 1 == m && curDate.getDate() == d) {
				return '' + this.zeroize(H) + ':' + this.zeroize(i);
			} else {
				var newDate = new Date((curTimestamp - 86400) * 1000); // 
				if (newDate.getFullYear() == Y && newDate.getMonth() + 1 == m && newDate.getDate() == d) {
					return '' + this.zeroize(H) + ':' + this.zeroize(i);
				} else if (curDate.getFullYear() == Y) {
					return this.zeroize(m) + '' + this.zeroize(d) + ' ' + this.zeroize(H) + ':' + this.zeroize(i);
				} else {
					return Y + '' + this.zeroize(m) + '' + this.zeroize(d) + ' ' + this.zeroize(H) + ':' + this.zeroize(i);
				}
			}
		}
	}
}